// src/app/favorites/page.tsx
'use client';

import { useState, useEffect } from 'react';
import { Box, Typography, Container } from '@mui/material';
import { FavoriteBorder } from '@mui/icons-material';
import CharityCard from '@/components/features/home/CharityCard';
import { getAllCharities } from 'lib/charities';
import { getFavorites } from '@/utils/favorites';
import type { Charity } from 'lib/charities';
import { colors } from '@/theme';
import { SimpleHeader } from '@/components/features';

const FavoritesPage = () => {
  const [favoriteCharities, setFavoriteCharities] = useState<Charity[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Load favorites from localStorage
    const favoriteIds = getFavorites();
    const allCharities = getAllCharities();
    const favorites = allCharities.filter(charity => favoriteIds.includes(charity.id));
    setFavoriteCharities(favorites);
    setIsLoading(false);
  }, []);

  // Refresh favorites when page gains focus (in case user favorited from details page)
  useEffect(() => {
    const handleFocus = () => {
      const favoriteIds = getFavorites();
      const allCharities = getAllCharities();
      const favorites = allCharities.filter(charity => favoriteIds.includes(charity.id));
      setFavoriteCharities(favorites);
    };

    window.addEventListener('focus', handleFocus);
    return () => window.removeEventListener('focus', handleFocus);
  }, []);

  if (isLoading) {
    return (
      <Container maxWidth="md" sx={{ py: 4 }}>
        <Typography>Loading...</Typography>
      </Container>
    );
  }

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        height: '100vh',
      }}
    >
      <SimpleHeader pageTitle="favorites" />
      {favoriteCharities.length > 0 ? (
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',
            gap: 2,
          }}
        >
          {favoriteCharities.map(charity => (
            <CharityCard key={charity.id} charity={charity} />
          ))}
        </Box>
      ) : (
        <Box
          sx={{
            textAlign: 'center',
            py: 8,
          }}
        >
          <FavoriteBorder
            sx={{
              fontSize: 80,
              color: colors.text.disabled,
              mb: 2,
            }}
          />
          <Typography
            variant="h6"
            sx={{
              color: colors.text.secondary,
              mb: 1,
            }}
          >
            No favorites yet
          </Typography>
          <Typography
            variant="body2"
            sx={{
              color: colors.text.disabled,
            }}
          >
            You haven't added any charities to your favorites yet...
          </Typography>
        </Box>
      )}
    </Box>
  );
};

export default FavoritesPage;
